<script type="text/javascript">
import bus from "@/bus.js"
import {mapState, mapGetters, mapMutations} from 'vuex' //stategettersmutations
import anime from 'animejs/lib/anime.es.js';
import renderTemp from '../plugins/renderTemp.js'
import * as PIXI from 'pixi.js'
import _ from 'lodash'
// import watermark from '../../node_modules/watermarkjs/dist/watermark.min.js'
import watermark from '../plugins/watermark.js'

export default {
	name: 'design',
	props: {

	},
	data(){
		return {
			loading_all: true,
			loading_per: false,
			loading_count: 0,
			download_tips_dia: false,
			download_tips_index: Number,
			select_label_index: -1,
			need_render: this.$route.params.need_render, // 12
			renderTempateData: this.$route.params.renderTempateData,
			origin_design_data: [
				// {	
				// 	'id': 1, 
				// 	'path': require('@/assets/design/design_1.jpg'),
				// 	'price' : 10,
				// 	'isCollect': false,
				// 	'level': 'temporary',
				//	 'labels': ['', '', '']
				// },
			],
			origin_design_data_len: 0,
			design_labels: ['', '', '', '', '', ''],
		}
	},
	watch: {
		
	},
	computed: {
		...mapState(['author_id', 'design_lists', 'department_id', 'currentUser', 'department_name']),
		canvas_: function(){
			return new PIXI.Application({
				backgroundColor: 0xf5f5f5,
				width: 2500,
				height: 2500,
				antialias: 1
			});
		},
		download_obj: function() {
			return {
				path: this.filter_design_data[this.download_tips_index].path,
				id: this.filter_design_data[this.download_tips_index].id,
				price: this.filter_design_data[this.download_tips_index].price
			}
		},
		filter_design_data: function() {
			// 
			const _select_label_index= this.select_label_index;
			if(_select_label_index != -1) {
				const _select_label = this.design_labels[_select_label_index]
				const reg = new RegExp(_select_label,'ig')
				const _pushArr=[];
				this.origin_design_data.map(function(item) {
					item.labels.map((label) => {
						if(label.search(_select_label) != -1){
							_pushArr.push(item);
						}
					})
				})
				return _pushArr
			}
			return this.origin_design_data;
		}

	},
	mounted: function() {
		this.hoverFunc()
	},
	methods: {
		...mapMutations(['setDesignLists']),
		prevFunc: function() {
			const me =this;
			// 
			me.$router.go(-1)
		},
		design_img_load: function() {
			// loadloading
			this.loading_count+=1;
			console.log(this.loading_count)
			if( this.loading_count >= 2){
				console.log('show')
				this.loading_all = false; //loading
				this.loading_per = true; //loading
			}
			if(this.loading_count >= this.origin_design_data_len){
				this.loading_per = false
			}

		},
		collectDesign: function(index, id, level) {
			const me =this;
			const tar =this.filter_design_data[index];
			// 
			tar.isCollect = !tar.isCollect;
			me.setDesignLists(me.origin_design_data) //store
			let render = 1;  // 1 2 
			if(level == 'permanent') {
				render = 2;
			}else {
				render = 1
			}
			if(!tar.isCollect) {
				// test
				me.cancel_collect_func(id, render)
			}else{
				me.to_collect_func(id, render)
			}
		},
		to_collect_func: function(template_id, render) {
			// 
			// template_id: id
			// render  1 2 
			const me = this;
			me.axios.post("/Collection/add_collection",{
				template_id: template_id,
				is_user: render
			})
			.then(function(data) {
				// console.log(data)
				me.$message('');
			})
			.catch(function(e) {
				console.log(e)
			})
		},
		cancel_collect_func: function(template_id, render) {
			// 
			const me = this;
			me.axios.get(`/Collection/change_collection?template_id=${template_id}&is_user=${render}`)
			.then(function(data) {
				// console.log(data)
				me.$message('');
			})
			.catch(function(e) {
				console.log(e)
			})
		},
		// 
		downsTips: function(index){
			this.download_tips_dia = true;
			this.$set(this, 'download_tips_index', index)
			// this.download_tips_index = index 
			//

		},

		// 
		downsFunc: function() {
			const me =this;
			this.axios.post("/Order/add_order",{
				template_id: me.download_obj.id,
				user_name: me.currentUser,
				department_id: me.department_id,
				department_name: me.department_name,
				price: me.download_obj.price
			})
			.then(function(data) {
				console.log(data)
				me.downs(me.download_obj.path, true); //
				me.download_tips_dia = false //
				me.$message('')
			})
			.catch(function(e) {
				console.log(e)
				me.$message('')
			})
			
		},
		// 
		selectLabel: function(flag, index=-1){
			this.select_label_index = index;
		},
		// 
		hoverFunc: function() {
			let cards = document.querySelectorAll(".cards");
			let range = 15;
			let calcValue = function calcValue(a, b) {return (a / b * range - range / 2).toFixed(1);}; 
			let timeout = void 0;
			cards.forEach(function (card) {
				card.addEventListener('mousemove', function (_ref) {
					let x = _ref.x,
					y = _ref.y;
					if (timeout) {
						window.cancelAnimationFrame(timeout);
					}

					timeout = window.requestAnimationFrame(function () {
						let yValue = calcValue(y, window.innerHeight);
						let xValue = calcValue(x, window.innerWidth);
						// console.log(xValue, yValue);


						card.style.transform = "translateX(" + -xValue + "px) translateY(" + yValue + "px)";
					});
				}, false);

			})
		},
		// loading
		// loadingFunc_: function() {
		// 	let pic
		// },
		
		//base64
		encodeFunc: function(src) {
			// const me =this;
			let encode = encodeURI(src);
			return btoa(encode);
		},

		// 
		
		// 
		handle_origin_data: function(datas) {
			// path
			datas.map((data) => {
				data.isCollect = false;
				// data.renderTempId = '';  renderTemId
				// data.render_preview_img = ''; //render_preview_img
				data.path = 'http://ht.idealead.hbindex.com/uploadFiles/images/' + data.path
			})
			// console.log('', datas)
			return datas
		},
		renderTempate: function(index, data) {
			console.log(index)
			const me =this;
			let	render_datas = {};  //
			let renderTempateData = _.cloneDeep(me.renderTempateData); //

			// console.log('',renderTempateData)

			render_datas.tempId = data[index].id; 
			render_datas.app =me.canvas_;
			render_datas.expand = false;
			Object.assign(render_datas, renderTempateData); //

			console.log('', render_datas)

			//  {tempId , app, expand=false, }
			renderTemp.tempInit(render_datas) 
			.then(backData => {
				console.log(': ',backData);
				
				data[index].id = backData.renderTempId; //idid
				// data[index].path = backData.render_preview_img; //
				data[index].path = me.encodeFunc(backData.render_preview_img); //
				data[index].level = 'temporary'; //

				me.origin_design_data.push(data[index]); //origin_design_data
		        me.setDesignLists(me.origin_design_data)

				me.judgeProcess(index, data) // 
			})
			.catch(_ => {

			});
		},
		// 
		judgeProcess: function(index, data) {
			const me =this;
			const templatesDatasLength = data.length - 1;
			console.log(templatesDatasLength)
			if(index < templatesDatasLength) {
		        // 
		        me.renderTempate(index+1, data)
		    }else {
		        //
		        // console.log('', data)
		        // me.origin_design_data = data;
		        // me.setDesignLists(data)
		        
		    }
		},

	},
	updated: function() {
		this.hoverFunc()
	},
	beforeRouteEnter(to, from, next) {
